<template>
    <div class="query-menu">
        <p>Beri gambaran mengenai masalah yang sedang kamu hadapi</p>
        <form v-on:submit.prevent="handleTemukanPesan(kategori,tema)">
            <div class="form-group">
                <label for="exampleInputEmail1">Masalahku berkaitan dengan</label>
                <select class="form-control sambat-select" id="kategori" v-model="kategori" @change="handleChangeKategori(kategori)">
                    <option value="" disabled>Pilih sebuah kategori</option>
                    <option v-for="(kategori, index) in getKategori" v-bind:key="index" :value="index">{{kategori}}</option>
                </select>
            </div>
            <div class="form-group">
                <label for="exampleInputPassword1">Yaitu</label>
                <select class="form-control sambat-select" id="tema" v-model="tema">
                    <option value="" disabled>Lalu tentukan sebuah tema spesifik</option>
                    <option v-for="(tema, index) in getTema" v-bind:key="index" :value="index">{{tema}}</option>
                </select>
            </div>
            <button type="submit" class="mx-0 btn-block btn-blue">Temukan Pesan</button>
        </form>
        <router-link to="/" class="navigation-link"><small>&#8592; Menu Utama</small></router-link>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    data() {
        return {
            kategori: '',
            tema: ''
        }
    },
    computed: {
        ...mapGetters([
            'getKategori',
            'getTema',
            'getPesan'
        ])
    },
    methods: {
        ...mapActions([
            'loadTema','temukanPesan'
        ]),
        handleChangeKategori(data){
            this.loadTema(data)
        },
        handleTemukanPesan(kategori,tema){
            if(kategori === ''){
                alert('Kategori tidak boleh kosong')
            } else {
                if(tema === ''){
                    alert('Tema harus dipilih')
                } else {
                    let data = {kategori,tema}
                    this.temukanPesan(data)
                    this.$router.push('/baca-pesan')
                }
            }
        }
    }
}
</script>

