import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'welcome',
    component: () =>import('../components/home-menu.vue')
  },
  {
    path: '/temukan-pesan',
    name: 'temukan-pesan',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../components/query-pesan.vue')
  },
  {
    path: '/menu',
    name: 'main-menu',
    component: () => import('../components/main-menu.vue')
  },
  {
    path: '/baca-pesan',
    name: 'baca-pesan',
    component: () => import('../components/baca-pesan.vue')
  },
  {
    path: '/tulis-pesan',
    name: 'tulis-pesan',
    component: () => import('../components/tulis-pesan.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
