import Vue from 'vue'
// import App from './App.vue'
import Welcome from './views/welcome.vue'
import router from './router'
import store from './store'

import navBar from './components/includes/navbar.vue'
import Quotes from './components/typing-quotes.vue'
import homeMenu from './components/home-menu.vue'
import mainMenu from './components/main-menu.vue'
import queryPesan from './components/query-pesan'

Vue.config.productionTip = false

Vue.component('navbar-component', navBar)
Vue.component('quotes', Quotes)
Vue.component('home-menu', homeMenu)
Vue.component('main-menu', mainMenu)
Vue.component('query-pesan', queryPesan)

new Vue({
  router,
  store,
  render: h => h(Welcome)
}).$mount('#app')
